.userinfo {
  height: calc(100vh - 1rem);
  margin-top: 1rem;
  width: 100%; }
  .userinfo .content {
    margin: 0 auto; }
    .userinfo .content .inputItem {
      width: 100%;
      margin-top: 0.1rem; }
      .userinfo .content .inputItem .label {
        text-align: right;
        width: 28%; }
      .userinfo .content .inputItem .input {
        padding-right: 0.1rem;
        border: 0.01rem solid #ddd;
        border-radius: 0.05rem; }
      .userinfo .content .inputItem .btn {
        width: 40%;
        margin-top: 30%; }
  .userinfo .am-list-item.am-input-item:after {
    border: 0; }
  .userinfo .am-input-clear {
    position: absolute;
    right: 0.1rem; }



