.worklog {
  height: calc(100vh - 1rem);
  margin-top: 1rem;
  width: 100%; }
  .worklog .content {
    margin: 0 auto; }
    .worklog .content .inputItem {
      width: 100%;
      margin-top: 0.1rem; }
      .worklog .content .inputItem .label {
        text-align: right;
        width: 28%; }
      .worklog .content .inputItem .input {
        width: 70%;
        padding-right: 0.1rem;
        border: 0.01rem solid #ddd;
        border-radius: 0.05rem; }
      .worklog .content .inputItem .btn {
        width: 40%;
        margin-top: 30%; }
    .worklog .content .projectItem {
      margin-left: 2%;
      width: 100%; }
      .worklog .content .projectItem .input {
        width: 88%;
        border: 0.01rem solid #ddd;
        border-radius: 0.05rem; }
      .worklog .content .projectItem .am-input-extra {
        display: none !important; }
  .worklog .am-list-item.am-input-item:after {
    border: 0; }
  .worklog .am-input-clear, .worklog .am-textarea-clear {
    position: absolute;
    right: 0.1rem; }
  .worklog .select_style {
    width: 78%;
    height: 0.5rem;
    border: 0.01rem solid #ccc;
    outline-style: none;
    text-indent: 0.1rem;
    font-size: 0.16rem;
    color: #333;
    background: #fff; }
  .worklog .aui-input-outline {
    padding: 0 0.15rem; }



