.employee {
  height: calc(100vh - 2rem);
  margin-top: 1rem; }
  .employee .valid {
    background-color: #ddd;
    color: gray; }
  .employee .label {
    margin-bottom: 0.1rem;
    text-align: center;
    width: 0.2rem;
    overflow: hidden;
    text-overflow: ellipsis; }
  .employee .name {
    text-align: left; }
  .employee .right {
    text-align: right; }



